const graphTypes  = [ "scatter" , "bar" ];
const graphModes  = [ "lines" , "lines+markers" , "markers" ];

const COLORS = [
  "red","green","blue","#b59a14","#b514a5","black","orange"
];

class WeatherGraph extends Component {

  constructor(props) {
    super(props);

    this.state = {
        isLoaded: false,
        graphType: graphTypes[0],
        graphMode: graphModes[1],
        latestWeatherConditions: null,
        latestRawData: null,
        offset: 24 * 5 * 2
    };
  }

  componentDidMount() {
    this.getRawWeatherDataFromApi(this.state.offset);

    setTimeout(()=>{
      console.log("reloading page after every 2.5 min :" , new Date());
      window.location.reload();
    } , 1000 * 60 * 2.5);
  }

  getRawWeatherDataFromApi = (offset) => {
    this.setState({isLoaded: false , });

    fetch(API_URL + offset)
    .then(res => res.json())
    .then(
        (result) => {
            this.setState({
              rawData: result.data
            });
            this.plotDataForWeather();
            this.showLatestData();
        },
        (error) => {
          console.log("ERROR :" ,error);
        }
    );

  }

  showLatestData = () => {
    /* 
    this will help in showing lastest data in the top card 
    */
    const latestWeatherConditions = this.state.rawData[this.state.rawData.length-1];
    const latestRawData = JSON.parse(latestWeatherConditions.rawData);

    this.setState({
      latestWeatherConditions,
      latestRawData
    });
  }

  cleanDataForPlotting = (type_of_data) => {
    let plotDatas = [];

    const randomColor = Math.floor(Math.random() * COLORS.length);

    let x = [] , y = [];
    this.state.rawData.forEach(data => {
        x.push(new Date(parseInt(data.timestamp)));
        y.push(data[type_of_data]);
    });

    plotDatas.push(
    {
        x: [ ...x ],
        y: [ ...y ],
        type:this.state.graphType,
        mode: this.state.graphMode,
        marker: {color: COLORS[randomColor]},
        name: `${type_of_data}`
    }
    );

    
    return plotDatas;
  }

  plotDataForWeather = () => {
    this.setState({isLoaded:false});
    setTimeout(()=>{
      this.setState({
        dataForPlot_humidity: this.cleanDataForPlotting(types_of_data[0]),
        dataForPlot_pressure: this.cleanDataForPlotting(types_of_data[1]),
        dataForPlot_temperature: this.cleanDataForPlotting(types_of_data[2]),
        isLoaded: true
      });
    },250);
    
  }

  degToCompass = (degree) => {
    const val=parseInt((degree/22.5)+.5);
    const arr=["N","NNE","NE","ENE","E","ESE", "SE", "SSE","S","SSW","SW","WSW","W","WNW","NW","NNW"];
    return arr[(val % 16)];
  }

  render() {
    console.log(FILE_NAME + " render() : this.state :" ,this.state);
    const width = window.innerWidth * 0.94;
    const height = window.innerHeight * 0.94;

    return (
      <div>
        <div style={{textAlign:"right"}}>
          <select onChange={e=>{
            this.getRawWeatherDataFromApi(e.target.value);
          }}>
            <option value="">Select no of items</option>
            <option value={0} key={"all__a"}>All items</option>
            <option value={10} key={"10__a"}>10</option>
            <option value={100} key={"100__a"}>100</option>
            <option value={1000} key={"1000__a"}>1000</option>
            <option value={10000} key={"10000__a"}>10000</option>
            <option value={100000} key={"100000__a"}>10000</option>
            <option value={1000000} key={"1000000__a"}>1000000</option>
          </select>
        </div>

        {(this.state.isLoaded) ? 
        <Card>
          <CardBody>
            <h2>Temperature : {Number(this.state.latestWeatherConditions.temperature).toFixed(2)} º C</h2>
            <h2>Humidity : {this.state.latestWeatherConditions.humidity} % </h2>
            <h2>Pressure : {this.state.latestWeatherConditions.pressure} hPa </h2>
            <h2>Cloudiness : {this.state.latestRawData.clouds.all} % </h2>
            <h2>Sunrise : {new Date(this.state.latestRawData.sys.sunrise * 1000).toLocaleTimeString()} AM</h2>
            <h2>Sunset : {new Date(this.state.latestRawData.sys.sunset * 1000).toLocaleTimeString()} PM</h2>
            
            <h2>Visibility : {this.state.latestRawData.visibility} meters </h2>
            <h2>Wind -speed : {(this.state.latestRawData.wind.speed * 3.6).toFixed(2)} kmph </h2>
            <h2>Wind -direction : {this.degToCompass(this.state.latestRawData.wind.deg)}</h2>
            <h2>{this.state.latestRawData.weather[0].main} {weatherDescription[this.state.latestRawData.weather[0].id]}<img height="60px" src={`https://openweathermap.org/img/wn/${this.state.latestRawData.weather[0].icon}@4x.png`} /></h2>
            
            <h5>last updated : {this.state.latestWeatherConditions.dateAndTime}</h5>
          </CardBody>
        </Card>
        : <Loading /> 
        }
        {
          this.state.isLoaded ?
          (<div>

            <span>
              <b>Change Type :</b>&nbsp;&nbsp;
              <Button color="primary" size="lg" onClick={()=>{this.setState({graphType: "scatter"}); this.plotDataForWeather(); }}>scatter TYPE</Button>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <Button color="warning" size="lg" onClick={()=>{this.setState({graphType: "bar"}); this.plotDataForWeather(); }}>bar TYPE</Button>
              &nbsp;&nbsp;&nbsp;&nbsp;
              CURRENT Type : [ { this.state.graphType } ]
              <br /><br />
            </span>

            <span>
              <b>Change Mode :</b>&nbsp;&nbsp;
              <Button color="primary" size="lg" onClick={()=>{this.setState({graphMode: "lines"}); this.plotDataForWeather(); }}>lines MODE</Button>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <Button color="danger" size="lg" onClick={()=>{this.setState({graphMode: "lines+markers"}); this.plotDataForWeather(); }}>lines+markers MODE</Button>
              &nbsp;&nbsp;&nbsp;&nbsp;
              <Button color="warning" size="lg" onClick={()=>{this.setState({graphMode: "markers"}); this.plotDataForWeather(); }}>markers MODE</Button>
              &nbsp;&nbsp;&nbsp;&nbsp;
              CURRENT Mode : [ { this.state.graphMode } ]
              <br /><br />
            </span>

            <Plot
            showlegend={false}
            data={ this.state.dataForPlot_temperature }
            layout={ {width, height, title: 'Temperature - Celsius' } }
            />

            <br /><hr />

            <Plot
            showlegend={false}
            data={ this.state.dataForPlot_humidity }
            layout={ {width, height, title: 'Humidity - percent' } }
            />

            <br /><hr />

            <Plot
            showlegend={false}
            data={ this.state.dataForPlot_pressure }
            layout={ {width, height, title: 'Pressure - Bar' } }
            />
          </div> ) : <Loading />
        }

      </div>
    );
  }
}

export default WeatherGraph;
